/* global Phaser */

import 'phaser'
import { assign } from 'lodash'
import images from './assets/images'

import { config } from './config'

let platform

class BootGame extends Phaser.Scene {
  constructor() {
    super('BootGame')
  }
  preload() {
    platform = this
  }
  create() {
    platform.textures.addBase64('tank', images.body)
    platform.textures.addBase64('cannon', images.cannon)
    platform.textures.addBase64('missile', images.bullet)
    platform.textures.addBase64('fire', images.muzzleflash)
    platform.textures.addBase64('background', images.arena)
    let remaining = 5

    platform.textures.on('onload', function() {
      if (--remaining === 0) this.game.scene.start('MainScene')
    })

  }
}

const GAME_CONFIG = {
  type: Phaser.AUTO,
  parent: 'simulation-arena',
  width: config.arena.width,
  height: config.arena.height,
  // transparent: true,
  // pixelArt: true,
  physics: {
    default: 'matter',
    matter: {
      debug: false
    }
  }
}

export function createGame(...scenes) {
  return new Phaser.Game(assign({}, GAME_CONFIG, {
    scene: [BootGame, ...scenes]
  }))
}
